import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/views/Home.vue';
import Wizard from '@/views/Wizard.vue';

Vue.use(Router);

const routerBase = window.location.origin === 'https://q42.github.io' ? '/publeaks' : '/';

export default new Router({
  mode: 'history',
  base: routerBase,
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  },
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/nieuwe-melding',
      name: 'nieuwe-melding',
      component: Wizard,
      props: { href: 'nieuwe-melding' },
    },
    {
      path: '/mijn-meldingen',
      name: 'mijn-meldingen',
      component: Wizard,
      props: { href: 'mijn-meldingen' },
    },
  ],
});
