














import { Component, Vue } from 'vue-property-decorator';

@Component
export default class HowToUpload extends Vue {
  private showPopover = false;
  private onionUrl: string = '';

  private created() {
    this.onionUrl = this.$t('onion.url') as string || '';
  }

  private flashPopover() {
    this.showPopover = true;
    setTimeout(() => this.showPopover = false, 2000);
  }
}
