import Vue from 'vue';
import VueI18n from 'vue-i18n';
import axios from 'axios';

Vue.use(VueI18n);

export default async function() {
  return new VueI18n({
    locale: 'nl',
    fallbackLocale: 'nl',
    messages: {
      nl: (await axios.get(`nl.lang.json`)).data,
    },
   });
}
